<template>
  <layout-bgi>
    <h2 class="register-header">用户注册</h2>
    <a-form :form="form" @submit.prevent="doLogin" layout="vertical" class="register-form">
      <a-form-item label="账号">
        <a-input
          v-decorator="[
          'account',
          {rules: [{ required: true, message: '账号不能为空！' }]}
        ]"
          placeholder="请输入账号"
        />
      </a-form-item>
      <a-form-item label="密码">
        <a-input
          v-decorator="[
          'password',
          {rules: [{ required: true, message: '密码不能为空！' }]}
        ]"
          placeholder="请输入密码"
        />
      </a-form-item>
      <a-form-item>
        <a-button html-type="submit" type="primary" block>注册</a-button>
      </a-form-item>
      <a-form-item style="text-align: center;">
        <span style="color: #f0f0f0;">
          已有账号？
          <router-link style="color: #333;" to="/login">去登录</router-link>
        </span>
      </a-form-item>
    </a-form>
  </layout-bgi>
</template>

<script>
import LayoutBgi from '@/components/layouts/LayoutBgi'

export default {
  name: 'register',
  components: { LayoutBgi },
  data () {
    return {
      form: this.$form.createForm(this)
    }
  },
  methods: {
    doLogin () {
      this.form.validateFields((err, values) => {
        if (!err) return
        console.log('todo register, data is', JSON.stringify(values))
      })
    }
  }
}
</script>

<style scoped>
.register-header {
  color: #fff;
  text-align: center;
  padding: 60px 0 30px;
}

.register-form {
  width: 30%;
  margin: 0 auto;
  padding-bottom: 80px;
}

.register-form >>> .ant-form-item-label label {
  color: #fff;
}
</style>
